import React, { forwardRef } from "react";

import { DropDown } from "../DropDown";

import { ButtonGroupButtonProps } from "./ButtonGroup.types";
import ButtonGroupButton from "./ButtonGroupButton";


const ButtonGroupButtonWithDropDown = forwardRef<HTMLButtonElement, ButtonGroupButtonProps>(({
	dataQA,
	buttonText,
	icon,
	isSelected,
	dropDownMenuProps,
	dropDownItems,
	$isDisabled,
	onClick,
	className,
	buttonSize,
	ariaLabel,
	leftIcon,
	rightIcon,
	...props
}, ref) => (
	<DropDown
		trigger={
			<ButtonGroupButton
				buttonText={buttonText}
				isSelected={isSelected}
				dataQA={dataQA}
				icon={icon}
				ariaLabel={ariaLabel}
				$isDisabled={$isDisabled}
				onClick={onClick}
				className={className}
				buttonSize={buttonSize}
				leftIcon={leftIcon}
				rightIcon={rightIcon}
				ref={ref}
				{...props} />
		}
		{...dropDownMenuProps}
		dataQA={dropDownMenuProps?.dataQA || `${dataQA}-dropdown`}
		ariaLabel={dropDownMenuProps?.ariaLabel || `${ariaLabel}-dropdown`}
	>
		{dropDownItems}
	</DropDown>


));


ButtonGroupButtonWithDropDown.displayName = "ButtonGroupButtonWithDropDown";
export default ButtonGroupButtonWithDropDown;

